import React, { Component } from "react";
import Auxillary from "../Auxillary/Auxillary";
import "./Layout.css";
import SideDrawer from "../../Components/HomePage/SideDrawer/SideDrawer";
import Toolbar from "../../Components/Navigation/ToolBar/ToolBar";

class Layout extends Component {
  state = {
    sideDrawerVisible: false,
  };

  sideDrawerHandler = () => {
    this.setState({ sideDrawerVisible: false });
  };

  toggleHandler = () => {
    this.setState((prevState) => {
      return { sideDrawerVisible: !prevState.sideDrawerVisible };
    });
  };

  render() {
    return (
      <Auxillary>
        <Toolbar open={this.toggleHandler} scroll={475} />
        <SideDrawer
          close={this.sideDrawerHandler}
          open={this.state.sideDrawerVisible}
        />
      </Auxillary>
    );
  }
}

export default Layout;
