import React, { Component } from 'react';
import { Link } from 'react-router-dom';
import './NavBarMobile.css';
import logo from '../../../../Assets/Images/logo.svg'
import Auxillary from '../../../../HOC/Auxillary/Auxillary';

class NavBarMobile extends Component {

    render() {
        return (
            <Auxillary>
            <div className="Nav-top">
                        <Link to = "/app/main" className = "Nav-top-logo">
                            <img src = {logo} alt = "Logo" />
                        </Link>
                    </div>    
                    <ul className="Nav-mainMenu-mobile ul-reset-mobile">
                        <li className="Nav-Submenu-mobile">
                            <span className = "suite-we-offer">
                                    Services We Offer
                            </span> 
                            <ul className="mobile-view">
                                <li>
                                <Link to = "/invoicing"  style={{textDecoration: "none"}} className="Nav-linkli-mobile" >Invoicing</Link>
                                </li><li>
                                <Link to = "/invoicing"  style={{textDecoration: "none"}} className="Nav-linkli-mobile" >Invoicing</Link>
                                </li>
                                <li>
                                <Link to = "/invoicing"  style={{textDecoration: "none"}} className="Nav-linkli-mobile" >Invoicing</Link>
                                </li>
                                <li>
                                <Link to = "/invoicing"  style={{textDecoration: "none"}} className="Nav-linkli-mobile" >Invoicing</Link>
                                </li>
                                <li>
                                <Link to = "/invoicing" style={{textDecoration: "none"}} className="Nav-linkli-mobile" >Invoicing</Link>
                                </li>
                            </ul>
                            </li>
                        <li className = "link-mobile">
                        <Link className="Nav-link-mobile" style={{textDecoration: "none"}} to="/pricing">Pricing</Link>
                        </li>
                        <li className = "link-mobile">
                        <Link className="Nav-link-mobile" style={{textDecoration: "none"}} to="/reviews">Reviews</Link>
                        </li>
                        <li className = "link-mobile">
                        <Link className="Nav-link-mobile" style={{textDecoration: "none"}} to="/About">About</Link>
                        </li>
                        <li className = "link-mobile">
                        <Link className="Nav-link-mobile" style={{textDecoration: "none"}} to="/gopro">Go PRO</Link>
                        </li>
                    </ul>
                    <ul className="Nav-webApp u-reset-ul">
                        <li className = "Nav-signUp-mobile">
                        <Link className="Nav-webApp-link-mobile Nav-webApp-link Nav-webApp-link--signUp Nav-signUpButton signupLink Nav-link-mobile" style={{textDecoration: "none"}} to="/signup">
                            <span>Sign up for free</span>
                        </Link>
                        </li>
                        <li className = "Nav-logIn-mobile">
                        <Link className="Nav-webApp-link Nav-webApp-link--signIn Nav-signInButton Nav-link-mobile" style={{textDecoration: "none"}} to="/login">
                            Log In
                        </Link>
                        </li>
                    </ul>
                </Auxillary>
        )
    }
}

export default NavBarMobile